import logo from './logo.svg';
import './App.css';
import { useEffect, useState } from "react";

function App() {
  const [status, setStatus] = useState(false);

  // getStatus sends http request to backend to demostrate how FE-BE communication
  const getStatus = () => {
    setStatus(false); // reset status hardcoded here

    fetch("http://localhost/api/v1/liveness/").then((res) => {
      if (res) {
        setStatus(res.status === 200);
      }
    });
  };

  useEffect(() => {
    getStatus();
  }, []);

  return ( 
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>Status: {status ? "Ready" : "Not Ready"}</p>
        <button onClick={getStatus}>Update Status</button>
    </header>
    </div>
  );
}

export default App;
